<template>
    <div class="message-info" v-loading="loading">
        <div class="content" :class="{mobile:$isMobile()}">
            <div class="msg-tit">
                {{ megInfo.title }}
            </div>
            <div class="msg-time">
                {{ $L('时间') }}：
                {{ megInfo.dtCreate }}
            </div>
            <div class="msg-summary" v-html="megInfo.summary">
            </div>
            <div class="msg-info ql-container ql-snow">
                <div class="ql-editor" v-html="megInfo.content"></div>
            </div>
        </div>
        <div class="bottom">
            <el-button class="btn" type="primary" @click="backFunc">{{ $L('返回') }}</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "messageInfo",
        data() {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                megInfo: {}
            }
        },
        created() {
            this.getData();
        },
        methods: {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc() {
                this.$router.push({name:'information'});
            },
            getData() {
                if (this.$route.params.id !== '') {
                    const _self = this;
                    _self.loading = true;
                    let id = _self.$route.params.id;
                    let arr = {
                        id: id
                    };
                    _self.$api('readContentReleaseNews', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.megInfo = reData.data;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    });
                }
            },

        }
    }
</script>

<style lang="scss" scoped>
    .message-info {
        position: absolute;
        width: 100%;
        padding: 20px;
        left: 0;
        top: 0;

        .content {
            margin: 10px 0;
            background: #fff;
            padding: 60px 80px 150px;

            &.mobile{
                padding: 20px 20px 30px;
            }

            .msg-tit {
                font-size: 20px;
                text-align: center;
                font-weight: 700;
            }

            .msg-time {
                text-align: center;
                padding-top: 15px;
            }

            .msg-info {
                padding-top: 15px;
                border: none;
            }

            .msg-summary {
                text-align: center;
                padding-top: 15px;
                color: #999;
            }
        }

        .bottom {
            text-align: right;
            padding: 20px;
            border-top: 2px solid #efefef;
            background: #fff;

            .btn {
                padding: 10px 30px;
            }
        }

    }
</style>